<template>
  <div id="app">
    <!-- <span class="scroll-info">X轴滚动距离：{{scroll}},滚动长度:{{clientHeight}}.... count:{{count}} </span> -->
      <keep-alive include="index">
        <router-view class="router"></router-view>
      </keep-alive>
  </div>
</template>

<script>
import { mapActions,mapState } from 'vuex'
import Page from './components/pages'
export default {
  name: 'App',
  data(){
    return{
      transitionName:'forward',
      loading:true,
      content:'正在进入...',
      // scroll:0,
      // clientHeight:0,
      // count:0
    }
  },
  mounted(){
    this.bus.$on('loading',(res)=>{
      this.loading = res.loading;
      this.content = res.content;
    })
    setTimeout(()=>{
      this.loading = false;
    },800)
  },
  watch: {
    '$route' (to, from) {
      const toDepth = to.path.split('/').length
      const fromDepth = from.path.split('/').length
      this.transitionName = toDepth < fromDepth ? 'back' : 'forward'
    }
  },
  components:{
    Page
  }
}
</script>

<style lang="stylus">
#app 
  min-height 100vh
  background-color #fff
body
  margin 0 
  padding 0
.forward-enter-active {
  animation: bounce-in .3s cubic-bezier(.36,.66,.04,1);
}
.forward-leave-active {
  animation: bounce-out .3s cubic-bezier(.36,.66,.04,1);
}
.back-enter-active {
  animation: back-in .3s cubic-bezier(.36,.66,.04,1);
}
.back-leave-active {
  animation: back-out .3s cubic-bezier(.36,.66,.04,1);
}
@keyframes bounce-in {
  0% {
    opacity 0
  }
  100% {
    opacity 1
  }
}
@keyframes bounce-out {
  0% {
    transform: translate(0,0) ;
    opacity 1
  }
  100% {
    opacity 0
    transform: translate(-100%,0) ;
  }
}
@keyframes back-in{
  0% {
    opacity 0
  }
  100% {
    opacity 1
  }
}
@keyframes back-out{
  0% {
    opacity 1
    transform: translate(0,0) ;
  }
  100% {
    transform: translate(100%,0) ;
    opacity 0
  }
}
</style>



