import Vue from 'vue'
import Router from 'vue-router'
import promise from 'es6-promise'
promise.polyfill();
Vue.use(Router);
export default new Router({
    mode:'history',
    scrollBehavior(to, from, savedPosition) {
        if(to.name ==='detail'||to.name ==='sharegroup'){
            return {x: 0, y: 0}
        }
    },
    routes:[
        {
            name:'index',
            path:'/',
            component:()=>import('./pages/index')
        },
        {
            name:'one',
            path:'/one/:id',
            component:()=>import('./pages/one')
        },
        {
            name:'article',
            path:'/article/:id',
            component:()=>import('./pages/article')
        },
        {
            name:'question',
            path:'/question/:id',
            component:()=>import('./pages/question')
        },
    ]
})





