<script>
export default {
    name:'OMCurtain',
    props:{
        show:Boolean,
        showClose:{
            type:Boolean,
            default:true
        }
    },
    methods:{
        handleClose(e){
            this.$emit('close',e)
        },
        defaultAction(){
            return false
        }
    }
}
</script>
<template>
    <transition name="fade">
        <div 
        class="om-curtain"
        v-if="show"
        >
            <div
            class="om-mask"
            @touchmove.prevent="defaultAction"
            @touchmove.stop="defaultAction"
            ></div>
            <div
            class="om-curtain--container"
            @touchmove.prevent="defaultAction"
            @touchmove.stop="defaultAction"
            >
            <div
            class="om-curtain--main"
            >
                <slot></slot>
            </div>
            <!-- <i 
            class="iconfont icon-close-circle om-curtain--close"
            @click="handleClose"
            ></i> -->
            <img 
            class="om-curtain--close"
            src="/static/imgs/close@2x.png"
            v-show="showClose"
             @click="handleClose"
             alt="">
            </div>
        </div>
    </transition>  
</template>


