<template>
  <div class="page">
    <OMButton
    @click="goto(obj.pre)"
    >
    <Icon value="left" />
        前一篇
    </OMButton>
    <div class="mid">
        <span>{{obj.month}}</span>
        -
        <span>{{obj.num}}</span>
    </div>
    <OMButton
    @click="goto(obj.next)"
    >后一篇
        <Icon value="right" />
    </OMButton>
  </div>
</template>

<script>
export default {
    name:'page',
    props:{
        obj:{
            default:() => ({
            }),
            type:Object
        }
    },
    methods:{
        goto(page){
            const { type } = this.obj
            if(page){
                this.$router.push({
                    name:type,
                    params:{id:page}
                })
            }else{
                this.$message('没有拉')
            }
        }
    },
}
</script>

<style lang="stylus" scoped>
.page
    width 100%
    position fixed
    bottom 0
    display flex
    justify-content space-between
    padding .2rem 
    .mid 
        line-height .71rem
        span 
            font-size .3rem
            line-height .71rem
</style>


